import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { withSuspense } from '../withSuspense'
import { selectProjectSamplesByProjectId } from '../../data/selectors'
import { SamplesDataGrid } from '../SamplesDataGrid'

interface ProjectDetailProps {
  projectId: number
}

export const ProjectSamples = withSuspense(
  ({ projectId }: ProjectDetailProps): JSX.Element => {
    const projectSamples = useRecoilValue(selectProjectSamplesByProjectId(projectId))
    return <SamplesDataGrid samples={projectSamples} />
  },
  () => <Skeleton height="3em" variant="rectangular" />
)
