import gql from 'graphql-tag'
import { PaginatedRecords, Box as GraphBox, BoxTaxa as GraphBoxTaxa } from '@namcbugdb/common'

import { Api } from '../api'
import { BoxTaxaReport } from '../../types'

export const fetchBoxTaxaReportByBoxIdQuery = gql`
  query ($offset: Int, $boxId: Int!) {
    boxes(boxIds: [$boxId], limit: 1, offset: 0) {
      records {
        sampleCount
        customerName
      }
    }
    boxTaxa(boxId: $boxId, limit: 1000, offset: $offset) {
      records {
        order
        family
        taxonomyId
        scientificName
        taxonomistCounts
        sampleCount
        totalCount
      }
      nextOffset
    }
  }
`

export const fetchBoxTaxaReportByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<BoxTaxaReport | null> => {
    const data = await api.queryAllPages<{
      boxes: PaginatedRecords<GraphBox>
      boxTaxa: PaginatedRecords<GraphBoxTaxa>
    }>(fetchBoxTaxaReportByBoxIdQuery, { boxId }, { dataProperty: 'boxTaxa' })

    if (data.boxes.records.length === 0) return null

    return {
      boxId,
      customerName: data.boxes.records[0].customerName,
      sampleCount: data.boxes.records[0].sampleCount,
      boxTaxa: data.boxTaxa.records.map(({ taxonomyId: id, scientificName: name, taxonomistCounts, ...rest }) => ({
        ...rest,
        id,
        name,
        taxonomistCounts: taxonomistCounts || {},
      })),
    }
  }
