import { WebMercatorViewport } from 'react-map-gl'

import { MAX_ZOOM } from './SitesMap'

export const calculateFit = (
  points: { latitude: number; longitude: number }[],
  { height, width, padding = 50 }: { height: number; width: number; padding?: number }
): { longitude: number; latitude: number; zoom: number } => {
  const updatedViewport = new WebMercatorViewport({ width, height })

  const minLat = Math.min(...points.map((site) => site.latitude))
  const maxLat = Math.max(...points.map((site) => site.latitude))
  const minLng = Math.min(...points.map((site) => site.longitude))
  const maxLng = Math.max(...points.map((site) => site.longitude))

  const { longitude, latitude, zoom } = updatedViewport.fitBounds(
    [
      [minLng, minLat],
      [maxLng, maxLat],
    ],
    { padding, maxZoom: MAX_ZOOM }
  )

  return { longitude, latitude, zoom }
}
